.container{
    margin: auto;
    max-width: 50%;
}

.done{
    text-decoration: line-through;
}

button{
    background-color: rgba(255, 255, 255, 0);
    border: none;
}

#add_bt{
border: 1px solid rgb(211, 207, 207);
background-color: rgba(128, 128, 128, 0.316);
margin-left: 5px;
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.item{
    font-size: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin-top: 15px;
    box-shadow: 0 2px 7px 1px rgba(0,0,0,0.2);
    word-break: break-all;
}

.card{
    font-weight: 600;
}
.card_okay{
    background-color: rgb(34, 216, 34);
    color: white;
    font-weight: 600;
}
